html {
  background-color: white;
  scroll-behavior: smooth;
  height: 100%;
}
html body .tail-select,
html body .tail-select.tail--multiple,
html body .tail-select.tail--single {
  border-radius: 8px;
  box-shadow: unset;
  border: 1px solid transparent;
}
html body .tail-select.city-select.tail-default.tail--single:hover {
  border: 1px solid #8c8c8c;
}

body {
  font-family: "Roboto", sans-serif;
  color: #2f2f2f;
  font-style: normal;
}

.container {
  padding: 0 20px;
  width: 1560px;
  margin-inline: auto;
}
@media (max-width: 1600px) {
  .container {
    width: 1180px;
    padding: 0 10px;
  }
}
@media (max-width: 1200px) {
  .container {
    width: 100%;
    padding: 0 10px;
    margin: 0;
  }
}

.switch-btns {
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  justify-content: center;
}
.switch-btns__btn {
  border-radius: 8px;
  padding-block: 8px;
  padding-inline: 40px;
  background: transparent;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: #2f2f2f;
  border: 1px solid #dbdbdb;
  transition: all 0.3s ease;
  cursor: pointer;
}
.switch-btns__btn:hover {
  background-color: #26ad60;
  color: #fff;
  border: 1px solid #26ad60;
}
.switch-btns__btn.active {
  background: #1f422e;
  color: #fff;
  border: 1px solid #1f422e;
  cursor: default;
}

.btn {
  border-radius: 8px;
  padding-block: 12px;
  padding-inline: 40px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  color: #fff;
  background: #1f422e;
  display: flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  gap: 10px;
  transition: all 0.3s ease;
  border: 1px solid transparent;
  cursor: pointer;
  text-transform: none;
  text-align: center;
}
@media (max-width: 1600px) {
  .btn {
    padding-inline: 20px;
  }
}
@media (min-width: 768px) {
  .btn:hover {
    background: #26ad60;
  }
}
.btn--transparent {
  background-color: transparent;
  color: #2f2f2f;
  border: 1px solid #26ad60;
}
@media (min-width: 768px) {
  .btn--transparent:hover {
    background-color: #26ad60;
    color: #fff;
  }
}
.btn--green-light {
  background-color: #26ad60;
  color: #fff;
  border: 1px solid #26ad60;
}
@media (min-width: 768px) {
  .btn--green-light:hover {
    background-color: #1f422e;
    color: #fff;
    border: 1px solid #1f422e;
  }
}
.btn--green-lighter {
  background-color: #eaf7f0;
  color: #2f2f2f;
  border: 1px solid #eaf7f0;
}
@media (min-width: 768px) {
  .btn--green-lighter:hover {
    background-color: rgb(38, 173, 96);
    color: #fff;
    border: 1px solid rgb(38, 173, 96);
  }
}
.btn--gray {
  background-color: #f8f8f8;
  color: #2f2f2f;
  border: 1px solid #f8f8f8;
}
@media (min-width: 768px) {
  .btn--gray:hover {
    background-color: #1f422e;
    color: #fff;
    border: 1px solid #1f422e;
  }
}
@media (min-width: 768px) {
  .btn--gray.no-hover:hover {
    background-color: #f8f8f8;
    color: #2f2f2f;
    border: 1px solid #f8f8f8;
  }
}
.btn--white {
  background-color: #fff;
  color: #2f2f2f;
  border: 1px solid #fff;
}
@media (min-width: 768px) {
  .btn--white:hover {
    background-color: #26ad60;
    color: #fff;
    border: 1px solid #26ad60;
  }
}
.btn--padding-20 {
  padding-inline: 20px;
}
.btn--small {
  padding: 8px 40px;
}
@media (min-width: 768px) {
  .btn--transparent-hover-white:hover {
    background-color: transparent;
    color: #fff;
    border: 1px solid #fff;
  }
}

.search-module {
  display: flex;
  border-radius: 8px;
  padding-block: 4px;
  padding-inline: 24px 4px;
  width: 300px;
  border: 1px solid #f5f5f5;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1600px) {
  .search-module {
    width: 180px;
    padding-inline: 12px 4px;
  }
}
.search-module .search__input {
  width: 100%;
}
.search-module .search__input input {
  border: unset;
  width: 100%;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #8c8c8c;
}
.search-module .search__btn {
  border-radius: 6px;
  padding: 7px;
  background: #f5f5f5;
  cursor: pointer;
  transition: all 0.3s ease;
}
.search-module .search__btn:hover {
  opacity: 0.5;
}

input[type=checkbox] {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background: transparent;
  border: 1px solid #f8f8f8;
  border-radius: 4px;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
  margin: 0;
}
input[type=checkbox]:checked {
  background: transparent;
  border-color: #f8f8f8;
}
input[type=checkbox]:checked::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  background-color: #f8f8f8;
  border-radius: 2px;
}
input[type=checkbox]:hover {
  border-color: rgb(222.5, 222.5, 222.5);
}

.lazyvideo {
  cursor: pointer;
}
.lazyvideo .video__placeholder {
  position: relative;
  height: 100%;
}
.lazyvideo .video__placeholder .placeholder__play {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.lazyvideo .video__placeholder .placeholder__play .play__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 67px;
  height: 67px;
  border-radius: 50%;
  background-color: #26ad60;
  transition: all 0.3s ease;
}
.lazyvideo .video__placeholder .placeholder__play .play__icon:hover {
  background-color: #1f422e;
}
.lazyvideo .video__placeholder picture, .lazyvideo .video__placeholder img {
  width: 100%;
  object-fit: cover;
  height: 100%;
  display: block;
}

.slider__container {
  width: 100%;
}
.slider__container .slider__controls {
  position: relative;
  display: flex;
  padding: 25px;
  width: 100px;
  margin-left: auto;
}

.tabs__control.active {
  border: 1px solid #1f422e;
  background-color: #1f422e;
  color: #fff;
}
.tabs__tab:not(.active) {
  display: none;
}

.dropdown {
  position: relative;
}
.dropdown-content {
  position: absolute;
  top: calc(100% + 10px);
  left: 0;
  display: flex;
  padding: 20px 10px;
  box-shadow: 0 1px 14px 0 rgba(47, 47, 47, 0.15);
  background: #fff;
  border-radius: 8px;
}
.dropdown .icon-opened {
  display: flex;
}
.dropdown .icon-closed {
  display: none;
}
.dropdown:not(.active) .icon-opened {
  display: none;
}
.dropdown:not(.active) .icon-closed {
  display: flex;
}
.dropdown:not(.active) .dropdown-content {
  display: none;
}

@media (min-width: 1200px) {
  .desktop-hidden {
    display: none !important;
  }
}

.locked-search, .locked-menu {
  overflow: clip;
}

.popup.popup-consult, .popup.popup-calc {
  padding: 0;
  border-radius: 16px;
  background-color: transparent;
}
.popup.popup-consult .f-button.is-close-button, .popup.popup-calc .f-button.is-close-button {
  top: 10px;
  right: 10px;
  opacity: 0.5;
}
@media (min-width: 768px) {
